.App {
  min-height: 100vh;
  background-color: #f8f9fa;
}

.app-header {
  text-align: center;
  padding: 40px 20px 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  margin-bottom: 0;
}

.app-header h1 {
  margin: 0 0 10px 0;
  font-size: 2.5em;
  font-weight: 700;
}

.app-header p {
  margin: 0 0 20px 0;
  font-size: 1.1em;
  opacity: 0.9;
}

.header-controls {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
}

.header-btn {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 10px 20px;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.2s ease;
  backdrop-filter: blur(10px);
}

.header-btn:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.5);
  transform: translateY(-2px);
}

.header-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

.results-section {
  padding: 0 20px;
}

.results-section h2 {
  text-align: center;
  color: #2c3e50;
  margin: 30px 0 20px 0;
  font-size: 1.6em;
  font-weight: 600;
}

.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 50vh;
  padding: 40px 20px;
}

.loading-container h2 {
  color: #2c3e50;
  margin-bottom: 30px;
  font-size: 1.5em;
}

.loading-container p {
  color: #6c757d;
  margin-top: 15px;
  font-size: 0.9em;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error-banner {
  background-color: #fff3cd;
  border: 1px solid #ffeaa7;
  border-radius: 8px;
  padding: 15px 20px;
  margin: 20px auto;
  max-width: 800px;
  text-align: center;
}

.error-banner p {
  margin: 5px 0;
  color: #856404;
}

.error-banner p:first-child {
  font-weight: 600;
}
